.search {
  position: relative;
  z-index: 1;
}
.search__top {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  display: flex;
  justify-content: flex-end;

  width: 100%;
  padding: 5px 0;

  box-shadow: 0 0 10px 2px gray;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background: linear-gradient(to top, white, rgba(255,255,255, 0.7));
}

.select {
  margin: 0 10px;
}
