.list {
  margin-top: 60px;
}

.my-masonry-grid {
  display: flex;
  margin-left: -10px;
  width: 100%;
}
.my-masonry-grid_column {
  padding-left: 10px;
  background-clip: padding-box;
}

.my-masonry-grid_column > div {
  background: grey;
  margin-bottom: 30px;
}

.image {
  padding: 2px;
  margin: 2px;

  max-width: 100%;
  transition: box-shadow 0.5s;
}

.image:hover {
  cursor: pointer;
  box-shadow: 0 0 10px 1px white;
}

